.main {
  &__header {
    text-align: center;
  }
  &__form {
    margin: 10px auto;
    position: relative;
  }
}
.hidden {
  visibility: hidden;
  z-index: -1;
  position: absolute;
}
.form__input {
  width: 100%;
}
@media all and (min-width: 768px) {
  .form__input {
    width: 50%;
  }
  .main {
    &__form {
      text-align: center;
    }
  }
}
